import _ from 'lodash';
import React, { useState } from 'react';
import Card from '@material-ui/core/Card';
import {
  makeStyles,
  Typography,
  CardContent,
  CardActions,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  TextField,
  DialogActions,
} from '@material-ui/core';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import LightbarList from './lightbar-list';
import { getLightbar, selectLightbar } from './lightbar-selector-slice';
import ColorSchemaList from './color-schema-list';
import { getSchemaName } from '../LightbarSchema/lightbar-shema-slice';

const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexDirection: 'row',
    alignContent: 'stretch',
    justifyContent: 'center',
    minHeight: 400,
    marginTop: 20,
  },
  card: {
    width: 500,
  },
  subtitle: {
    fontSize: 14,
  },
  button: {
    justifyContent: 'flex-end',
  },
});

export function LightbarSelector() {
  const classes = useStyles();
  const history = useHistory();
  const dispatch = useDispatch();
  const selectedLightbar = useSelector(getLightbar);
  const selectedColorSchema = useSelector(getSchemaName);
  const [shouldAskForNumberOfLights, askForNumberOfLights] = useState(false);

  // const routeToLightbar = () => history.push('/lightbar');
  const routeToLightbar = () => {
    if(selectedLightbar.isCustom && selectedLightbar.total == 0){
      askForNumberOfLights(true);
      return;
    }
    askForNumberOfLights(false);
    history.push('/lightbar/schema');
  }

  const closeDialog = () => askForNumberOfLights(false);
  const handleCustomLightbarChanges = (e) => {
    const lightbar = _.cloneDeep(selectedLightbar);
    lightbar.front = parseInt(e.target.value);
    lightbar.total = lightbar.front;

    if(!lightbar.front.isNaN){
      dispatch(selectLightbar(lightbar));
    }
  };

  return (
    <div className={classes.root}>
      <Card className={classes.card}>
        <CardContent>
          <Typography variant="h6">Lightbars</Typography>
          <Typography className={classes.subtitle}>
            Select a lightbar model to configure
          </Typography>
          <LightbarList />
          <Typography variant="h6">Color Schema</Typography>
          <Typography className={classes.subtitle}>
            Select the color schema of the lightbar
          </Typography>
          <ColorSchemaList />
        </CardContent>
        <CardActions className={classes.button}>
          <Button
            color="primary"
            disabled={!selectedLightbar || !selectedColorSchema}
            variant="contained"
            onClick={routeToLightbar}
          >
            Start Configurator
          </Button>
        </CardActions>
      </Card>
      <Dialog open={shouldAskForNumberOfLights} onClose={closeDialog}>
        <DialogTitle>Custom Lightbar</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Enter the number of lights
          </DialogContentText>
          <TextField
            autoFocus
            fullWidth
            id="numberOfLight"
            label="Number of lights"
            margin="dense"
            type="number"
            value={selectedLightbar.front}
            onChange={handleCustomLightbarChanges}
          />
        </DialogContent>
        <DialogActions>
          <Button color="primary" onClick={routeToLightbar}>
            Continue
          </Button>
          <Button
            color="primary"
            onClick={closeDialog}
          >
            Back
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
